@import './../comon/styles/sass/variebles.scss';
@import './../comon/styles/sass/mixins.scss';
.mainBlock {
    min-height: 100vh;
    @include block();
    .container {
        .content {
            max-width: 770px;
            width: 100%;
            margin-left: auto;
            .text {
                font-family: 'Inter', sans-serif;
                color: white;
                //border: 1px solid white;
                h1 {
                    font-size: 78px;
                    letter-spacing: -0.2px;
                    line-height: 90px;
                    margin-bottom: 43px;
                    font-weight: 300;
                    @media screen and (max-width: 540px ) {
                        font-size: 35px;
                        letter-spacing: -0.2px;
                        line-height: 50px;
                        margin-bottom: 43px;
                        font-weight: 300;
                        margin-left: 100px;
                    }
                    @media screen and (max-width: 459px ) {
                        font-size: 25px;
                        letter-spacing: -0.2px;
                        line-height: 50px;
                        margin-bottom: 43px;
                        font-weight: 300;
                        margin-left: 180px;
                    }
                    @media screen and (max-width: 290px ) {
                        font-size: 19px;
                        letter-spacing: -0.2px;
                        line-height: 50px;
                        margin-bottom: 43px;
                        font-weight: 300;
                        margin-left: 215px;
                    }
                    span {
                        color: $primaryColor;
                    }
                }
            }

            .description {
                max-width: 480px;
                margin-bottom: 31px;
                color: $secondTextColor;
            }

            .href {
                width: 175px;
                height: 175px;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-left: auto;
                border-radius: 50%;
                border: 1px solid #575757;
                position: relative;
                overflow: hidden;
                margin-right: 15px;
                @media screen and (max-width: 624px ) {
                    margin-left: 10px;
                    margin-right: auto;
                }
                    .icon {
                        position: absolute;
                        color: #fff;
                        //font-size: 60px;
                        display: block;
                        &:hover {
                            color: $primaryColor;
                            transition: all 0.3s ease-in-out 0s;
                        }
                    }
            }
            .facts {;
                gap: 100px;
                row-gap: 100px;
                column-gap: 100px;
                margin-top: 55px;
                margin-left: 10px;
                display: flex;
                @media screen and (max-width: 624px ) {
                    column-gap: 50px;
                    justify-content: center;
                }
                @media screen and (max-width: 456px ) {
                    flex: none;
                    flex-direction: column;
                    row-gap: 10px;
                    align-items: center;
                }


                //border: 1px solid white;
            }
        }
    }

}

.linkWrapper {
    width: 680px;
    //margin-left: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media screen and (max-width: 624px ) {
        flex-direction: column;
        height: 300px;
    }
}


.img {
    animation: spin 4s infinite linear;
}
@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}
.cv {
    font-family: 'Inter', sans-serif;
    font-size: 20px;
    text-decoration: none;
    color: $textColor;
    border: 1px $secondTextColor solid;
    border-radius: 50px;
    padding: 20px;
    margin-top: 30px;
    &:hover {
        color: $primaryColor;
        transition: all 0.3s ease-in-out 0s;
        border: 1px $primaryColor solid;
    }
}








